:root {
  --primary-color: #2196f3;
  --primary-dark: #1976d2;
  --error-color: #f44336;
  --text-primary: #2c3e50;
  --text-secondary: #666;
  --background-primary: #f4f4f4;
  --background-secondary: #fff;
  --border-color: #e0e0e0;
  --shadow-sm: 0 2px 4px #0000000d;
  --shadow-md: 0 4px 12px #0000000d;
  --shadow-lg: 0 6px 16px #0000001a;
  --transition-speed: .3s;
}

body {
  background-color: var(--background-primary);
  color: var(--text-primary);
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica Neue, sans-serif;
}

.site-header {
  text-align: center;
  background: var(--background-secondary);
  border-bottom: 1px solid var(--border-color);
  margin-bottom: 2rem;
  padding: 2rem 1rem;
}

h1 {
  color: var(--text-primary);
  margin: 0 0 1rem;
  font-size: 2rem;
}

.description {
  max-width: 800px;
  color: var(--text-secondary);
  margin: 0 auto;
  font-size: .95rem;
  line-height: 1.5;
}

.main-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

.controls-panel {
  background: var(--background-secondary);
  box-shadow: var(--shadow-md);
  border-radius: 12px;
  margin-bottom: 2rem;
  padding: 1.5rem;
}

.parameters-container {
  margin-bottom: 1.5rem;
}

.parameters-grid {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
  display: grid;
}

.parameter-section {
  transition: background-color var(--transition-speed);
  background-color: #f8f9fa;
  border-radius: 8px;
  padding: 1rem;
}

.parameter-section:hover {
  background-color: #f0f2f5;
}

.parameter-section h3 {
  color: var(--text-primary);
  margin: 0 0 1rem;
  font-size: 1.1rem;
}

.parameter-row {
  gap: 1rem;
  margin-bottom: .5rem;
  display: flex;
}

.parameter-group {
  flex: 1;
  align-items: center;
  gap: .5rem;
  display: flex;
}

.parameter-group label {
  color: var(--text-secondary);
  text-align: right;
  min-width: 35px;
  font-size: .9rem;
}

.input-wrapper {
  transition: all var(--transition-speed);
  flex: 1;
  align-items: center;
  gap: .25rem;
  display: flex;
  position: relative;
}

.input-wrapper.error {
  animation: .4s ease-in-out shake;
}

.input-wrapper.error input {
  border-color: var(--error-color);
  box-shadow: 0 0 0 1px var(--error-color);
  background-color: #f443360d;
}

.input-wrapper.error:after {
  content: "!";
  color: var(--error-color);
  text-align: center;
  background-color: #f443361a;
  border-radius: 50%;
  width: 16px;
  height: 16px;
  font-weight: bold;
  line-height: 16px;
  position: absolute;
  right: -20px;
}

.input-wrapper.error:hover:before {
  content: attr(data-error);
  background-color: var(--error-color);
  color: #fff;
  white-space: nowrap;
  box-shadow: var(--shadow-md);
  z-index: 1;
  border-radius: 4px;
  padding: 6px 12px;
  font-size: .8rem;
  position: absolute;
  bottom: calc(100% + 8px);
  left: 50%;
  transform: translateX(-50%);
}

.input-wrapper.error:hover:after {
  content: "";
  border-left: 6px solid #0000;
  border-right: 6px solid #0000;
  border-top: 6px solid var(--error-color);
  z-index: 1;
  position: absolute;
  bottom: calc(100% + 2px);
  left: 50%;
  transform: translateX(-50%);
}

@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }

  25% {
    transform: translateX(-4px);
  }

  75% {
    transform: translateX(4px);
  }
}

.input-wrapper span {
  color: var(--text-secondary);
  font-size: .9rem;
}

input[type="number"] {
  border: 1px solid var(--border-color);
  width: 100%;
  color: var(--text-primary);
  transition: all var(--transition-speed);
  border-radius: 6px;
  padding: .5rem;
  font-size: .9rem;
  box-shadow: inset 0 1px 3px #0000000d;
}

input[type="number"]:focus {
  border-color: var(--primary-color);
  outline: none;
  box-shadow: 0 0 0 3px #2196f31a;
}

.tooltip {
  color: var(--text-secondary);
  margin-top: .25rem;
  font-size: .8rem;
}

.simulation-tabs {
  border-bottom: 1px solid var(--border-color);
  gap: 1rem;
  margin-bottom: 1.5rem;
  display: flex;
}

.tab-button {
  color: var(--text-secondary);
  cursor: pointer;
  transition: color var(--transition-speed);
  background: none;
  border: none;
  padding: .75rem 1.5rem;
  font-size: 1rem;
  position: relative;
}

.tab-button:hover {
  color: var(--primary-color);
}

.tab-button.active {
  color: var(--primary-color);
  font-weight: 600;
}

.tab-button.active:after {
  content: "";
  background-color: var(--primary-color);
  width: 100%;
  height: 2px;
  position: absolute;
  bottom: -1px;
  left: 0;
}

.tab-panel {
  display: none;
}

.tab-panel.active {
  display: block;
}

.primary-button {
  background-color: var(--primary-color);
  color: #fff;
  cursor: pointer;
  transition: all var(--transition-speed);
  box-shadow: var(--shadow-sm);
  text-align: center;
  border: none;
  border-radius: 8px;
  min-width: 200px;
  margin: 1rem 0;
  padding: .875rem 1.75rem;
  font-size: .9375rem;
  font-weight: 600;
  display: inline-block;
}

.primary-button:hover {
  background-color: var(--primary-dark);
  box-shadow: var(--shadow-md);
  transform: translateY(-1px);
}

.primary-button:active {
  transform: translateY(0);
}

.primary-button:disabled {
  background-color: var(--primary-color);
  opacity: .7;
  cursor: not-allowed;
  transform: none;
}

.primary-button.loading {
  color: #0000;
  pointer-events: none;
  position: relative;
}

.primary-button.loading:after {
  content: "";
  border: 2px solid #ffffff4d;
  border-top-color: #fff;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  margin: -10px 0 0 -10px;
  animation: .8s linear infinite button-loading-spinner;
  position: absolute;
  top: 50%;
  left: 50%;
}

.simulation-layout {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
  display: grid;
}

.visualization-container {
  background: var(--background-secondary);
  box-shadow: var(--shadow-md);
  border-radius: 12px;
  padding: 1.5rem;
}

.population-grids {
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  margin-bottom: 1.5rem;
  display: grid;
}

.grid-box {
  position: relative;
}

.grid-title {
  text-align: center;
  color: var(--text-primary);
  margin-bottom: .5rem;
  font-size: .9rem;
  font-weight: 600;
}

.grid {
  aspect-ratio: 1;
  border: 1px solid var(--border-color);
  background-color: var(--background-secondary);
  width: 100%;
  transition: all var(--transition-speed);
  background-image: linear-gradient(#0000000d 1px, #0000 1px), linear-gradient(90deg, #0000000d 1px, #0000 1px);
  background-size: 10% 10%;
  border-radius: 8px;
  box-shadow: inset 0 0 30px #00000005;
}

.grid:hover {
  border-color: #d0d0d0;
  box-shadow: inset 0 0 35px #00000008;
}

.unified-legend {
  background: var(--background-secondary);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  padding: 1rem;
}

.legend-title {
  text-align: center;
  color: var(--text-primary);
  margin: 0 0 1rem;
  font-size: 1rem;
}

.legend-content {
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  display: grid;
}

.legend-column h5 {
  color: var(--text-primary);
  margin: 0 0 .5rem;
  font-size: .9rem;
}

.legend-items {
  flex-direction: column;
  gap: .5rem;
  display: flex;
}

.legend-item {
  color: var(--text-secondary);
  transition: transform var(--transition-speed);
  align-items: center;
  gap: .5rem;
  font-size: .8rem;
  display: flex;
}

.legend-item:hover {
  transform: translateX(2px);
}

.legend-dot {
  box-shadow: var(--shadow-sm);
  border-radius: 50%;
}

.white-dot {
  background-color: #2196f3b3;
  border: 1px solid #2196f3e6;
}

.black-dot {
  background-color: #f44336b3;
  border: 1px solid #f44336e6;
}

.dot {
  transition: all var(--transition-speed);
  border-radius: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
}

.chart-section {
  background: var(--background-secondary);
  box-shadow: var(--shadow-md);
  border-radius: 12px;
  padding: 1.5rem;
}

.chart-title {
  text-align: center;
  color: var(--text-primary);
  margin: 0 0 1rem;
  font-size: 1rem;
}

.chart-container {
  aspect-ratio: 1;
  background-color: var(--background-secondary);
  width: 100%;
  box-shadow: inset var(--shadow-sm);
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  padding: .25rem;
  display: flex;
}

.chart-container.large {
  max-width: 800px;
  margin: 2rem auto;
}

.chart-container canvas {
  width: 100% !important;
  max-width: 100% !important;
  height: 100% !important;
  max-height: 100% !important;
}

@media (width <= 768px) {
  .parameters-grid, .simulation-layout, .population-grids, .legend-content {
    grid-template-columns: 1fr;
  }
}

@keyframes button-loading-spinner {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(1turn);
  }
}
